@import url(https://fonts.googleapis.com/css?family=Montserrat:500);

.hero{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: black;
    width: 100vw;
    height: calc(100vh - 80px);
    overflow: hidden;
}

.myVideo {
    min-height: 100%;
    opacity: 0.4;
}

.hero-text{
    font-size: calc(1vh + 1vw);
    color: #fff;
    position: absolute;
    align-items: center;
    text-align: center;
}

.hero-text > p {
    font-size: calc(0.8vh + 0.8vw)
}
.center{
    padding: 100px 0 50px 0;
    text-align: center;
    width: 100vw;
    font-size: calc(2vh + 2vw)
}

.contents{
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 100px 100px 100px;
}

.card{
    background-color: aliceblue;
    min-width: 300px;
    max-width: 400px;
    min-height: 400px;
    margin: 30px;
    cursor: pointer;
    transition: scale linear .5s, box-shadow linear .5s;
}

.card-image{
    overflow: hidden;
    height: 250px;
}

.card-image img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: scale ease-in-out .5s;
}

.card-title{
    width: 50%;
    height: 50px;
    background-color:#fff;
    border-radius: 50px;
    transform: translate(50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.card-desc{
    padding: 0 30px 20px 30px;
    word-break: keep-all;
    text-align: justify;
    transform: translate(0, -20px);
}

.card:hover{
    scale: 1.05;
    box-shadow: 10px 10px 27px -22px rgba(0,0,0,0.75);
}

.card img:hover{
    scale: 1.1;
}

.card img:not(:hover){
    scale: 1;
}

.card:not(:hover){
    scale: 1;
    box-shadow: none;
}
.container{
    width: 60%;
    height: 200px;
    margin: 10px;
    border: 2px black solid;
    box-shadow: 5px 5px 0px 0px rgba(0,0,0,0.75);
    text-align: center;
    overflow: hidden;
}

.container-first{
    transform: translate(100px);
}

.container-second{
    transform: translate(-100px);
}

.container-first img, .container-second img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    scale: 1.1;
    -webkit-filter: blur(1px);
            filter: blur(1px);
}

.container-text{
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.container:hover{
    cursor: pointer;
    scale: 1.02;
    transition: ease-in-out 0.3s scale;
}

.container:not(:hover){
    scale: 1;
    transition: ease-in-out 0.3s scale;
}

@media (max-width: 1000px){
    .container-first, .container-second{
        transform: none;
        width: 100%;
    }

    .contents {
        padding: 0 20px 100px 20px;
    }
}

@media (min-width: 1001px){
    .container-first{
        transform: translate(100px);
    }
    
    .container-second{
        transform: translate(-100px);
    
    }
}
.formRepair{
    display: block;
    width: 70vw;
    max-width: 1000px;
